.hamburger {
  display: none;
  width: 27px;
  height: 18px;
  position: relative;
  z-index: 7000;
  margin-left: auto;
  right: 20.5px;
  top: -44px;
}

.hamburger span {
  display: block;
  width: 27px;
  height: 3px;
  background-color: #267c82;
  position: absolute;
  transition: transform 1s;
}

.hamburger span:first-of-type {
  top: 0;
  right: 0;
}

.hamburger span:nth-of-type(2) {
  top: 7.5px;
  right: 0;
}

.hamburger span:last-of-type {
  bottom: 0;
  right: 0;
}

.js-hamburger.is-active span:first-of-type {
  transform: rotate(-45deg);
  top: 8px;
}

.js-hamburger.is-active span:nth-of-type(2) {
  display: none;
}

.js-hamburger.is-active span:last-of-type {
  transform: rotate(45deg);
  top: 8px;
}

.js-hamburger.is-active span {
  background-color: #fff;
}

